<template>
  <div>
    
    <div class="loginContainer">
    
      <el-descriptions title="回答" style="color: aliceblue;">
          <el-descriptions-item label="回答">{{ reply }}</el-descriptions-item>
      </el-descriptions>

      <div v-loading="loading"
            element-loading-text="思考中。。。"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)" class="load"/>

      <div class="abc">
        <el-input type="textarea" :autosize="{ minRows: 2, maxRows: 4}" placeholder="请输入内容" v-model="params.messages[1].content"/>
        <el-button type="primary" icon="el-icon-search" @click="submit2()">提问</el-button>
      </div>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "ask",
  data() {
    return {
      question: '',
      reply: '',
      loading: false,
      params: {
          "messages": [
            {
              "role": "system",
              "content": "请以markdown的形式返回答案"
            },
            {
              "role": "user",
              "content": ""
            }
          ],
          "tokensLength": 2333,
          "model": "gpt-3.5-turbo"
        }
    }
  },
  
  beforeCreate() {
    document.querySelector('body').classList.add("overall");
  },
  beforeDestroy() {
    document.querySelector('body').classList.remove("overall");
  },

  methods: {
    submit() {
      this.loading = true;
      this.getRequest("question?messages=" + this.question).then(resp => {
              console.log(resp)
              this.reply = resp;
              if(resp != undefined) {
                this.loading = false;
              }
              // if(resp.status == 200) {
              //   console.log(resp)
              // }
            });
    },

    submit2() {
      this.loading = true;
      axios({
        method: 'post',
        url: `https://api.aioschat.com`,
        data: this.params
    }).then(resp => {
              this.reply = resp.choices[0].text;
              if(resp != undefined) {
                this.loading = false;
              }
            });
    }
  }
}
</script>

<style>
.loginContainer {
  background-clip: padding-box;
  width: 100%;
  height: 100%;

}

.abc {
  position: absolute;
    bottom: 0px;
    width: 100%;
    display: flex;
}

.load {
  width: 100%;
  height: 50px;
  width: 100%;
  height: 50px;
  position: absolute;
  bottom: 52px;
}

.overall {
  position: absolute;
  content: ' ';
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  background-image: url(../assets/huahua.jpg);
  background-size: cover;
}
</style>
